.home {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    color: #FFF;
    align-items: center;
    mix-blend-mode: hard-light;

    p {
        margin: 0;
        padding: 0;
    }

    font-size: 0.7em;

    @media only screen and #{$phone} {
        font-size: 0.7em;
    }

    @media only screen and #{$small-phone} {
        font-size: 0.5em;
    }    

    @media only screen and #{$mid-point} and #{$height-above-iphone-5} {
        font-size: 1em;
    }

    .home-intro {
        max-width: 500px;

        .home-intro-title {
            transform: rotate(-8deg);
            margin-bottom: 20px;

            p.intro-leading {
                letter-spacing: 8px;
                font-size: 2em;

                span {
                    display: inline-block;
                    font-size: 4em;
                    letter-spacing: normal;
                    padding: 10px;
                    font-weight: bold;
                    font-family: $manuscrit-font;
                    color: $secondary-color;
                }
            }

            h1.intro-main {
                -webkit-text-stroke-width: 1px;
                -webkit-text-stroke-color: #FFF;
                -webkit-text-fill-color: transparent;
                font-family: $secondary-font;
                font-weight: bold;
                text-transform: uppercase;
                font-size: 5em;
            }
        }

        p.intro-description {
            font-size: 1.7em;
        }
    }

    .home-steps {
        margin-left: auto;
        margin-right: auto;

        ul {
            list-style-type: none;
            text-align: left;
            margin: 0 0 0 0.6em;
            padding: 0;

            li {
                font-size: 1.3em;
                margin: 30px 0;
                position: relative;

                &::before {
                    text-align: right;
                    position: absolute;
                    content: '';
                    font-family: $secondary-font;
                    font-size: 3em;
                    left: -0.6em;
                    top: 0;
                }

                &:nth-child(1) {
                    margin-left: 0;

                    &::before {
                        content: '1';
                    }
                }

                &:nth-child(2) {
                    margin-left: 1em;

                    &::before {
                        content: '2';
                    }
                }

                &:nth-child(3) {
                    margin-left: 2em;

                    &::before {
                        content: '3';
                    }
                }
            }
        }

        span.box {
            background-color: $secondary-color;
            font-family: $secondary-font;
            text-transform: uppercase;
            padding: 4px 6px;
            font-weight: bold;
        }
    }
}

.home-background {
    background-color: $primary-color;
    background: linear-gradient(315deg,
            rgba($primary-color, 1) 3%,
            rgba($secondary-color, 1) 38%,
            rgba($tertiary-color, 1) 68%,
            rgba($primary-color, 1) 93%);
    animation: gradient 15s ease infinite;
    background-size: 450% 450%;

    @keyframes gradient {
        0% {
            background-position: 0% 0%;
        }

        50% {
            background-position: 100% 100%;
        }

        100% {
            background-position: 0% 0%;
        }
    }
}