$primary-color: #292e4b;
// backgorund for white text, or secondary text

$secondary-color: #dda0bc;
// background for black text or primary text

$tertiary-color: #2e696a;
// elements (buttons, borders, etc) in light background

$links-color: #01a4ca;
$not-so-white: #fcfcfc;


$primary-font: 'Roboto Condensed', sans-serif;
$secondary-font: 'Lato', sans-serif;
$manuscrit-font: 'Oooh Baby', cursive;

/* Spacing
-------------------------------*/
$space: 20px;
$space-double: 40px;

/* Breakpoints
-------------------------------*/
$small-phone: "(max-width: 320px)";
$phone: "(min-width: 320px)";
$tablet: "(min-width: 480px)";
$mid-point: "(min-width: 620px)";
$desktop: "(min-width: 768px)";
$before-desktop: "(max-width: 768px)";

/* Breakpoints
-------------------------------*/
$screen-xs: 480px;
$screen-sm: 768px;
$screen-md: 992px;
$screen-lg: 1200px;

$bpoints: (
    'xsmall' : (max-width: $screen-xs),
    'lsmall' : (min-width: $screen-xs),
    'small' : (min-width: $screen-sm),
    'medium' : (min-width: $screen-md),
    'large' : (min-width: $screen-lg)
);

/* Height Breakpoints
-------------------------------*/
$height-above-iphone-5: "(min-height: 568px)";